import React,{Component} from 'react'
import { hashHistory } from 'react-router';
import { Layout, Breadcrumb, Button, Table, Popconfirm, message, Spin } from 'antd';
const { Content } = Layout;

export class List extends React.Component {
    constructor(props) {
        super(props);
        this.state ={
            loading: true,
            columns: [{
                title: "投递人",
                render: data =>
                    <div className="divClick" onClick={this.goToUserDetail.bind(this, data.userID)}>{data.userName}</div>

            }, {
                title: "投递内容",
                dataIndex: "content"
            }, {
                title: "投递文件",
                render: data =>
                    <div className="divClick" onClick={this.downloadFile.bind(this, data.fileLink)}>{data.fileName}</div>
            }, {
                title: "投递时间",
                dataIndex: "time"
            }, {
                title: "操作",
                render: data =>
                    <div style={{"textAlign": "center"}}>
                        <Button type="primary" onClick={this.detailInfo.bind(this, data.id)} style={{"marginRight": "20px"}}>查看
                        </Button>
                        <Popconfirm placement="left" title="是否确认删除?" onConfirm={this.deleteInfo.bind(this, data.id)} okText="确认" cancelText="取消">
                            <Button type="danger">删除</Button>
                        </Popconfirm>
                    </div>
            }],
            dataSource: [],
            pagination: {
                current: 1,
                total: 0
            }
        };
        ['loadListData', 'goToUserDetail', 'detailInfo', 'deleteInfo', 'changePage', 'downloadFile'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        this.loadListData(1);
    }

    goToUserDetail(id) {
        hashHistory.push("/user/manage/detail/" + id);
    }

    detailInfo(id) {
        hashHistory.push("/delegateFund/receivedProject/detail/" + id);

    }

    deleteInfo(id) {
        let that = this;
        ajax({
            url: "/delegateFund/receivedProject/deleteInfo",
            method: "post",
            data: {id},
            callback: function(data) {
                if(data.status == 200) {
                    message.success("删除成功", 1);
                    let newDataSource = that.state.dataSource.filter(item => { return item.id != id});
                    that.setState({dataSource: newDataSource});
                } else {
                    message.error(data.errMsg, 1);
                }
            }
        });
    }

    downloadFile(link) {
        location.href = link;
    }

    loadListData(page) {
        let that = this;
        ajax({
            url: "/delegateFund/receivedProject/getInfoList",
            method: "post",
            data:  {
                id: that.props.location.query.fundID,
                current: page
            },
            callback: function(data) {
                if(data.status == 200) {
                    that.setState({
                        pagination: {
                            current: data.data.current,
                            total: data.data.total
                        },
                        dataSource: data.data.list,
                        loading: false
                    })
                } else {
                    message.error(data.errMsg, 1);
                }
            }
        })
    }

    return() {
        history.back();
    }

    changePage(page) {
        this.loadListData(page.current);
    }

    render() {
        return (
            <Layout style={{ padding: '0 24px 24px' }}>
                <Breadcrumb style={{ margin: '12px 0' }}>
                    {
                        this.props.routes.map(function(item, index) {
                            return <Breadcrumb.Item key="">{item.breadcrumbName}</Breadcrumb.Item>
                        })
                    }
                </Breadcrumb>
                <Content style={{ background: '#fff', padding: 24, margin: 0, minHeight: 280 }}>
                    <Button style={{marginBottom: 30}} icon="rollback" onClick={this.return}>返回</Button>
                    <Spin spinning={this.state.loading} tip="Loading...">
                        <Table
                            bordered
                            dataSource={this.state.dataSource}
                            columns={this.state.columns}
                            pagination={this.state.pagination}
                            onChange={this.changePage}
                        />
                    </Spin>
                </Content>
            </Layout>
        )
    }
}

module.exports = List;


